/*
*
*  Noteboard CSS
*
*/

.addCard {
  margin-top: 30px;
  text-align: center;
  margin-right: 9px;
}

.card-body
{
  width: 205px;
  float: left;
}

.card-body-edit
{
  width: 163px;
  float: left;
}

.cards {
  display: block;
  margin-left: 76px;
  position: relative;
}

.paper {
  height: 170px;
  width: 170px;
  padding: 35px 15px 15px 15px;
  margin: 25px 25px 0px 0px;
  text-align: 'center';
  display: 'inline-block';
  word-break: normal;
  float: left;
  overflow: hidden;
  word-wrap: break-word;
}

.paperAdd {
  height: 170px;
  width: 210px;
  padding: 5px 15px 5px 3px;
  margin: 25px 25px 0px 0px;
  text-align: 'center';
  display: 'inline-block';
  word-break: break-all;
  float: left;
}

.paperEdit {
  height: 170px;
  width: 170px;
  padding: 5px 15px 5px 4px;
  margin: 25px 25px 0px 0px;
  text-align: 'center';
  display: 'inline-block';
  word-break: break-all;
  float: left;
  word-wrap: break-word;
}

