}

/*
*
*  Mobile Devices
*
*/

@media screen and (max-width: 750px) {
  .cv
  {
    font-family: 'Roboto', sans-serif;
    position: relative;
    width: 817px;
    height: 2125px;
    left: 13%;
    top: 50px;
    left: calc(50% - 190px);
    background: #FFFFFF;
    -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
    border-radius: 7px;
    margin-bottom: 13px;
    display: block;
  }

  .rightcolumn
  {
    left: 70%;
    position: absolute;
    margin-right: 30px;
    width: 170px;
  }

  .maincolumn
  {
    position: absolute;
    width: 61%;
    left: 5%;
  }

  .noteboard
  {
    position: absolute;
    top: 9px;
    z-index: 1;
    font-size: 15px;
    left: 70%;
    width: 817px;
    text-align: center;
  }

  .addCard 
  {
    margin-top: 30px;
    text-align: center;
    margin-right: 9px;
    position: relative;
    left: 3%;
  }

  .cards 
  {
    margin-left: 0.1px;
    float: left;
    margin-bottom: 3px;
    left: 2%;
  }

  .inputtext
  {
    position: relative;
    top: 17px;
    left: 25%;
    color: #00BCD4;
    border: 0px;
    -webkit-appearance: none;
    margin-bottom: 5px;
    width: 50%;
    display: block;
  }

  .inputtext:focus
  {
    position: relative;
    top: 17px;
    left: 25%;
    color: #00BCD4;
    border-bottom: 1px solid blue;
    -webkit-appearance: none;
    margin-bottom: 5px;
    width: 50%;
    display: block;
  }

  .mymodal {
    width: 213px;
    margin-top: 1%;
    margin-left: 32.1%;
  }  

  .paper {
    height: 170px;
    width: 170px;
    padding: 35px 15px 15px 15px;
    margin: 25px 17px 0px 0px;
    text-align: 'center';
    display: 'inline-block';
    word-break: normal;
    float: left;
    overflow: hidden;
    word-wrap: break-word;
  }
}

